  position: absolute;
  top: 50%;
  transform: translate(-50%, -50%);
  z-index: 5;
}

.video__thumbnail button:hover, .video__thumbnail button:focus {
  background-color: #cee0fb;
}

.video__thumbnail img {
  position: relative;
  width: 100%;
  z-index: 2;
}

.video__thumbnail svg {
  color: currentcolor;
  height: 24px;
  transform: translateX(5%);
  width: 24px;
}

.video__thumbnail:after {
  background-image: linear-gradient(to bottom, rgba(0, 0, 0, 0) 0%, rgba(0, 0, 0, 0.8) 100%);
  content: '';
  display: block;
  z-index: 2;
}

.video__content {
  color: #003057;
  font-family: "Roboto", arial, sans-serif;
  padding-top: 1rem;
}

.video__content h2 {
  font-size: 39px;
}

.video__content p {
  font-size: 20px;
}

.video__modal {
  --bs-modal-width: 50%;
}

.video__modal .modal-content {
  aspect-ratio: 16/9;
  background: transparent;
  border: 0;
  max-width: 1280px;
  position: relative;
}

.video__modal .modal-content button {
  background: transparent;
  border: 0;
  bottom: 100%;
  color: #ffffff;
  padding: 4px;
  position: absolute;
  right: 0;
}

.video__modal .modal-content button svg {
  fill: currentcolor;
  height: 24px;
  width: 24px;
}

.video__modal .modal-content button:hover, .video__modal .modal-content button:focus, .video__modal .modal-content button:active {
  color: #cee0fb;
}

@media screen and (min-width: 960px) {
  .video {
    margin-bottom: 60px;
    margin-top: 60px;
  }
}

@media screen and (min-width: 1200px) {
  .video .container {
    --bs-gutter-x: 7rem;
  }
}

